@import "../core/variables.scss";
.contact {
  width: 100%;
  height: 100vh;

  &__hero {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    padding: 0 10%;
    line-height: 80px;
    background: linear-gradient(
      to bottom,
      $backgroundColor 0%,
      $backgroundColor 60%,
      $mainColor 60%,
      $mainColor 100%
    );
    @media all and (min-width: 1024px) {
      padding-top: 40px;
    }
  }
  &__title {
    text-transform: uppercase;
    letter-spacing: -0.1px;
    width: 175px;
    height: auto;
    overflow-wrap: anywhere;
    font-size: clamp(7.8rem, 15vw, 22rem);
    font-weight: 900;
    @media all and (min-width: 768px) {
      width: 440px;
      line-height: 120px;
    }
    @media all and (min-width: 1024px) {
      width: 500px;
      line-height: 170px;
    }
    @media all and (min-width: 1440px) {
      width: 700px;
      line-height: 180px;
    }
  }

  &__container {
    display: flex;
    flex-direction: row;
    justify-content: right;

    @media all and (min-width: 1440px) {
      margin-top: 80px;
    }
  }
  &__text {
    font-size: clamp(1.6rem, 1.2vw, 2.4rem);
    color: #f8f8f8;
    font-weight: bold;
    line-height: normal;
    text-align: right;
    margin-top: 12rem;
  }
}
.contact__line {
  display: inline-block;
  width: 165px;
  height: 2%;
  margin-bottom: 10px;
  background-color: $mainColor;
  border-color: black;
}
